import Vue from "vue";
import Router from "vue-router";
import Index from "@/views/Index";
import Login from "@/views/Login";
import Home from "@/views/Home";
import Goods from "@/views/Goods";
import User from "@/views/User";
import Register from "@/views/Register";
Vue.use(Router);
export default new Router({
	routes: [
		{
			path: "/",
			redirect: "/home",
			name: "home",
			component: Index,
			children: [
				{
					path: "home",
					component: Home,
				},
				{
					path: "goods",
					component: Goods,
				},
			],
		},
		{
			path: "/login",
			name: "login",
			component: Login,
		},
		{
			path: "/register",
			name: "register",
			component: Register,
		},
		{
			path: "/user",
			name: "user",
			component: User,
			meta: {
				//需要守卫
				auth: true,
			},
		},
	],
});
